<template>
    <div>
       <header class="mui-bar mui-bar-nav">
			<h1 class="mui-title">老五</h1>
		</header>
		<div class="mui-content">
 
			<!--chat.wxml-->
			<div id="cont">
 
				<div >
 
					<!--<div v-if="{{item.type == 1}}">-->
					<div class="reply">
						<div class="pic">
							<image class="image" src="img/list_img.png"></image>
						</div>
						<div class="content">
							<div class="text">
								{{hh}}
							</div>
						</div>
					</div>
					<!--</div>-->
					<!--<div v-if="{{item.type == null}}">-->
					<div class="send">
						<div class="content">
							<div class="text">
								{{names}}
							</div>
						</div>
						<div class="pic">
							<image class="image" src="img/list_img.png"></image>
						</div>
					</div>
					<!--</div>-->
				</div>
 
				<div class="sendMessage">
					<input id="inputs" type="text" placeholder="请输入您要说的话" />
					<button id="btn_submit" @click="mit()">发送</button>
 
				</div>
 
			</div>
 
			<!--<scroll-view scroll-y="true" style='margin-bottom:120rpx;' scroll-top="{{scrollTop}}">-->
			<!--<div is="temp" data="{{message}}"></div>-->
			<!--</scroll-view>-->
 
		</div>
    </div>
    
</template>
<script>
import $ from 'jquery'
var str = "";
export default {
    data(){
        return{
            message: [
 
					],
					names: "what",
					hh:"你说啥",
					inputMsg: "",
					scrollTop: 0
        }
    },
    	methods: {
