							this.names = name;
                            document.getElementById("inputs").value="";
							//回复
							$.ajax({
								type: "get",
								url: "http://www.tuling123.com/openapi/api",
								data: {
									key: "fa7f4d06b0a24b479d29ea0a01672350",
									info: msg.content
								},
								success: function(data) {
									console.log(data, "111111111111");
//									var reply = {
//										type: 1,
//										src: "http://tu.maomaogougou.cn/picture/2015/05/c53b650c3a5fd847688118e566d6673f.png",
//										content: data.text
//									};
 
                                  str=data.text;
 
								}
							});
							
 
						}
 
						console.log(str,"str");
								this.hh=str;	
						//								this.Messages(reply);
					},
//					Message: function(msg) {
//						var list = this.message;
//						list.push(msg);
//						this.message = list;
//						console.log("Message", list);
//
//					},
//					Messages: function(msg) {
//						var list = this.message;
//						list.push(msg);
//						this.message = list;
//						console.log("2222222222", list);
//
//					},
				},
    mounted(){
    }
}
</script>
<style>
		page {
			background-color: #ececec;
		}
		
		.reply .pic,
		.send .pic {
			border-radius: 50%;
			padding: 10px;
		}
		
		.reply,
		.send {
			display: flex;
		}
		
		.reply .pic image,
		.send .pic image {
			border-radius: 50%;
			width: 80rpx;
			height: 80rpx;
		}
		
		.send {
			justify-content: flex-end;
		}
		
		.reply .content {
			padding: 10px 10px 10px 0;
			max-width: 480rpx;
		}
		
		.send .content {
			padding: 10px 0 10px 10px;
			max-width: 480rpx;
		}
		
		.reply .content .text,
		.send .content .text {
			background-color: #fff;
			border-radius: 5px;
			padding: 10px;
			font-size: 16px;
		}
		
		.send .content .text {
			background-color: #78fd75;
		}
		
		.sendMessage {
			display: flex;
			position: fixed;
			bottom: 0;
			left: 0;
			right: 0;
			padding: 10px;
			background-color: #fff;
		}
		
		.inputMsg {
			flex: 2;
			background-color: #fff;
			height: 30px;
			font-size: 16px;
			padding: 5px 10px;
			border-bottom: 1px solid #ddd;
		}
		
		.sendMessage {
			position: fixed;
			bottom: 0px;
			width: 100%;
			height: 60px;
			line-height: 60px;
			background-color: #4CD964;
		}
		
		#inputs {
			position: absolute;
			display: inline-block;
			width: 65%;
			height: 40px;
			background-color: #fff;
			border-radius: 3px;
		}
		
		#btn_submit {
			position: absolute;
			width: 100px;
			display: inline-block;
			right: 10px;
			height: 40px;
			background-color: #DD524D;
			border: 0px;
			text-align: center;
			color: #fff;
		}
		
		.image {
			
			width: 60px;
			border-radius: 50%;
			height: 60px;
		}
	</style>
 


